<template>
    <v-list three-line>
        <template v-for="(item, index) in items">
            <v-subheader
                    v-if="item.header"
                    :key="item.header"
                    v-text="item.header"
            ></v-subheader>

            <v-divider
                    v-else-if="item.divider"
                    :key="index"
                    :inset="item.inset"
            ></v-divider>

            <v-list-item
                    v-else
                    :key="item.title"
            >
                <v-list-item-avatar>
                    <v-img :src="item.avatar"></v-img>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title v-html="item.title"></v-list-item-title>
                    <span class="teal--text">
                        <v-icon color="teal">mdi-calendar-month</v-icon>
                        {{item.start}} - {{item.end}}
                    </span>
                    <!--v-list-item-subtitle v-html="item.summary"></v-list-item-subtitle-->
                </v-list-item-content>
            </v-list-item>
        </template>
    </v-list>
</template>

<script>
    export default {
        name: "List",
        data: () => ({
            items: [
                {
                    avatar: require('@/assets/WorkExperience/company.jpg'),
                    title: 'Company 1',
                    start: 'Sep 2019',
                    end: 'Current',
                    summary: '',
                },
                { divider: true, inset: true },
                {
                    avatar: require('@/assets/WorkExperience/company.jpg'),
                    title: 'Company 2',
                    start: 'Sep 2017',
                    end: 'Sep 2018',
                    summary: '',
                },
                { divider: true, inset: true },
                {
                    avatar: require('@/assets/WorkExperience/company.jpg'),
                    title: 'Company 3',
                    start: 'Jan 2017',
                    end: 'Feb 2017',
                    summary: ''
                }
            ],
        })
    }
</script>

