<template>
    <v-card>
        <v-img
                :src="profil"
                height="260px"
                dark
        >
            <v-row align="end" class="fill-height">
                <v-col>
                    <v-card-title>
                        <div class="display-1 white--text">{{ conf.profil.name.first + ' ' + conf.profil.name.family }}</div>
                    </v-card-title>
                </v-col>
            </v-row>
        </v-img>

        <v-list two-line>
            <v-list-item v-if="$t('jobTitle')">
                <v-list-item-icon>
                    <v-icon color="teal">{{ job.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ $t('jobTitle') }}</v-list-item-title>
                    <v-list-item-subtitle>{{ $t('jobSub') }}</v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>

            <v-divider inset v-if="mobile.title"></v-divider>

            <v-list-item v-if="mobile.title">
                <v-list-item-icon>
                    <v-icon color="teal">{{ mobile.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ mobile.title }}</v-list-item-title>
                    <v-list-item-subtitle>{{ mobile.subtitle }}</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-icon>
                    <v-btn icon :title="mobile.call" :href="'tel:' + mobile.title">
                        <v-icon>mdi-phone-ring</v-icon>
                    </v-btn>
                    <v-btn icon :title="mobile.sms" :href="'tel:' + mobile.title">
                        <v-icon>mdi-message-text</v-icon>
                    </v-btn>
                </v-list-item-icon>
            </v-list-item>

            <v-divider v-if="email.title" inset></v-divider>

            <v-list-item v-if="email.title">
                <v-list-item-icon>
                    <v-icon color="teal">{{ email.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ email.title }}</v-list-item-title>
                    <v-list-item-subtitle>{{ email.subtitle }}</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-icon>
                    <v-btn icon :title="email.message" :href="'mailto:' + email.title + '?subject=' + email.subject">
                        <v-icon>mdi-send</v-icon>
                    </v-btn>
                </v-list-item-icon>
            </v-list-item>

            <v-divider v-if="address.title" inset></v-divider>

            <v-list-item v-if="$t('addressTitle')">
                <v-list-item-icon>
                    <v-icon color="teal">{{ address.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ $t('addressTitle') }}</v-list-item-title>
                    <v-list-item-subtitle>{{ $t('addressSub') }}</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-icon>
                    <v-btn icon href="http://maps.google.com/maps?q=caen" :title="address.map">
                        <v-icon>mdi-map-marker-check</v-icon>
                    </v-btn>
                </v-list-item-icon>
            </v-list-item>

            <v-divider inset></v-divider>

            <v-list-item>
                <v-list-item-icon>
                    <v-icon color="teal">{{ skills.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ $t('skillsTitle') }}</v-list-item-title>
                    <CustomHorizontalBar :chartdata="skills.chartData"></CustomHorizontalBar>
                </v-list-item-content>
            </v-list-item>

            <v-divider inset></v-divider>

            <v-list-item>
                <v-list-item-icon>
                    <v-icon color="teal">{{ languages.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ $t('languagesTitle') }}</v-list-item-title>
                    <CustomHorizontalBar :chartdata="languages.chartData"></CustomHorizontalBar>
                </v-list-item-content>
            </v-list-item>

            <v-divider inset></v-divider>

            <v-list-item>
                <v-list-item-icon>
                    <v-icon color="teal">{{ hobbies.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ $t('hobbiesTitle') }}</v-list-item-title>
                    <div style="margin-top: 15px; width: 100%;">
                        <v-card dark color="#126349" outlined class="interest-item">
                            {{ $t('hobbiesList1') }}
                        </v-card>
                        <v-card dark color="#148161" outlined class="interest-item">
                            {{ $t('hobbiesList2') }}
                        </v-card>
                        <v-card dark color="#159f7c" outlined class="interest-item">
                            {{ $t('hobbiesList3') }}
                        </v-card>
                        <v-card dark color="#16c7a3" outlined class="interest-item">
                            {{ $t('hobbiesList4') }}
                        </v-card>
                    </div>
                </v-list-item-content>
            </v-list-item>
        </v-list>
    </v-card>
</template>

<script>
    import CustomHorizontalBar from "./CustomHorizontalBar";
    import json from '@/json/cv_conf.json';

    export default {
        name: "Profil",
        components: {CustomHorizontalBar},
        data: function () {
            return {
                conf: json,
                profil: require('@/assets/Profil/profil.jpg'),
                job: {
                    icon: 'mdi-briefcase',
                },
                mobile: {
                    icon: 'mdi-phone',
                    title: null,
                    subtitle: 'personal mobile phone',
                    call: 'Call me',
                    sms: 'Text me'
                },
                email: {
                    icon: 'mdi-email',
                    title: null,
                    subtitle: 'personal',
                    message: 'Send me an email',
                    subject: '[CV]%20Contact%20from%20CV%20app'
                },
                address: {
                    icon: 'mdi-map-marker',
                    map: 'Find me on a map'
                },
                skills: {
                    icon: 'mdi-asterisk',
                    title: 'Skills',
                    chartData: {
                        labels: ['PHP', 'Python', 'Java'],
                        datasets: [
                            {
                                backgroundColor: ['#16c7a3','#159978','#10795d'],
                                data: [70, 60, 40],
                            }
                        ]
                    }
                },
                languages: {
                    icon: 'mdi-earth',
                    title: 'Languages',
                    chartData: {
                        labels: ['Français', 'English', 'Italiano'],
                        datasets: [
                            {
                                backgroundColor: ['#16c7a3','#159978','#10795d'],
                                data: [85, 60, 55],
                            }
                        ]
                    }
                },
                hobbies: {
                    icon: 'mdi-golf',
                }
            }
        }
    }
</script>

<style>
    .v-list-item__content {
        display: initial;
    }
    .interest-item {
        padding: 7px;
        margin-right: 5px;
        margin-top: 3px;
        display: inline-block;
    }
    canvas{
        height:100px !important;
        width: 100% !important;
    }
</style>
