<template>
    <v-list three-line>
        <template v-for="(item, index) in items">
            <v-subheader
                    v-if="item.header"
                    :key="item.header"
                    v-text="item.header"
            ></v-subheader>

            <v-divider
                    v-else-if="item.divider"
                    :key="index"
                    :inset="item.inset"
            ></v-divider>

            <v-list-item v-else :key="item.title" v-on:click="actionItem">
                <v-list-item-avatar>
                    <v-img :src="item.avatar"></v-img>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-title v-html="item.title"></v-list-item-title>
                    <span class="teal--text">
                        <v-icon color="teal">mdi-calendar-month</v-icon>
                        {{item.start}} - {{item.end}}
                    </span>
                    <!--v-list-item-subtitle v-html="item.summary"></v-list-item-subtitle-->
                </v-list-item-content>
                <!--v-list-item-action>
                    <v-btn icon>
                        <v-icon color="grey lighten-1">mdi-information</v-icon>
                    </v-btn>
                </v-list-item-action-->
            </v-list-item>
        </template>
    </v-list>
</template>

