<template>
    <v-app-bar app color="teal" dark fixed padless>
        <v-app-bar-nav-icon @click="snackbar = true"></v-app-bar-nav-icon>
        <v-toolbar-title>CV</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-menu offset-y>
            <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                    <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
            </template>
            <v-list>
                <v-list-item
                        v-for="(item, index) in languages"
                        :key="index"
                        @click="changeLocale(item.language)"
                >
                    <v-list-item-title><flag :iso="item.flag" v-bind:squared=false /> {{ item.title }}</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>

        <v-snackbar v-model="snackbar">
            Nothing to display for the moment !
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-app-bar>
</template>

<script>
    import i18n from '@/plugins/i18n';
    export default {
        name: "Header",
        data: () => ({
            snackbar: false,
            languages: [
                { flag: 'us', language: 'en', title: 'English' },
                { flag: 'fr', language: 'fr', title: 'Français' },
                { flag: 'it', language: 'it', title: 'Italiano' },
                { flag: 'es', language: 'es', title: 'Español' }
            ]
        }),
        methods: {
            changeLocale(locale) {
                i18n.locale = locale;
            }
        }
    }
</script>

<style scoped>

</style>
